"use strict";
import React from "react";
import './App.css';
import { createApi } from 'unsplash-js';

//NAMN: Dan Heikenberg
//ID: ao0071

const unsplash = createApi({ accessKey: 'G0mr-66Lw3xTWMv-JJSLTRpxFAG7vASteAdWyLx0x4Q' });

let hideElements = true;

function App() {

  const [city, setCity] = React.useState("");
  const [cityImage, setCityImage] = React.useState("");  
  const [cityWeather, setCityWeather] = React.useState("");  
  const [cityTemp, setCityTemp] = React.useState("");  
  const [cityDesc, setCityDesc] = React.useState("");  
 
  function submitForm(e) {
    e.preventDefault();
    hideElements = false;
    //Hmtar vder-data frn Openweathermap.
    const url = "https://api.openweathermap.org/data/2.5/weather?q=" + city + "&units=metric&appid=264021a8f4b068b22394eb25a5813c38";
    fetch(url)
    .then(response => response.json())
    .then(data => { 
      if (data.main === undefined) { //Kollar om objectet (staden) finns, och gr val drefter.
        alert("Please check the spelling, or try another city.");
      } else {
        const temp = data.main.temp;
        const weatherDescription = data.weather[0].description;
        let imgSource = ("http://openweathermap.org/img/wn/"+ data.weather[0].icon + "@2x.png");        
        setCityWeather(imgSource);
        setCityTemp(temp);
        setCityDesc(weatherDescription);
      }
      //Hmtar bild-data frn Unsplash.
      unsplash.search.getPhotos({
        query: city      
      })
        .then(result=>{ //Kollar om objected (staden) finns, och gr val drefter.
          if (result.response.results[0] === undefined) {
            alert("Could not find an image from this city.");
          } else {
            setCityImage(result.response.results[0].links.download);
          }
      })
    })
  }

  return (
    <div className="App">
      <h1>City Weather!</h1>
      <p>(The image may not reflect the current weather.)</p>
      <form id="submitForm" onSubmit={submitForm}>
        <label htmlFor="city">City name:</label>
        <input type="text" id="city" onChange={(e) => setCity(e.target.value)} value={city}/><br></br>
        <button id="button" type="submit">Check City</button>
      </form>
      {hideElements === true ? (
      <div className="weatherContainerHide">
        <div className="tempAndDescContainer">
          <img src={cityWeather} alt="" width="100"/>
            <p>Weather description: {cityDesc}</p> 
            <p>Celsius: {cityTemp}</p>                  
          </div>
        <div className="imageContainer">
          <img src={cityImage} alt={city} width="400"/>
        </div>
      </div>)
      :
      (
        <div className="weatherContainer">
          <div className="tempAndDescContainer">
            <img src={cityWeather} alt="" width="100"/>
              <p>Weather description: {cityDesc}</p> 
              <p>Celsius: {cityTemp}</p>                  
            </div>
          <div className="imageContainer">
            <img src={cityImage} alt={city} width="400"/>
          </div>
        </div>)}
    </div>
  );
}

export default App;
